.logo {
  position: relative;
  padding: 10px;
  padding-bottom: 0;
}
.logo span {
  font-size: 14px;
  position: absolute;
  bottom: 3px;
  right: -43px;
  color: #35495E;
}
header .navigation a {
  position: relative;
  text-transform: uppercase;
  font-size: 18px;
  margin: 10px;
  color: #35495E;
  text-decoration: none;
}
header .navigation a:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #41B883;
  visibility: hidden;
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  -webkit-transition: all 0.3s ease-in-out 0s;
  transition: all 0.3s ease-in-out 0s;
}
header .navigation a:hover:before {
  visibility: visible;
  -webkit-transform: scaleX(1);
  transform: scaleX(1);
}
header .navigation .router-link-exact-active {
  color : #41B883;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}
header a:hover {
  color: #41B883;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}
